.image__animation {
  position: relative;
  z-index: 2;
  animation-name: up_down;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes up_down {
  0% {
    bottom: 0;
  }
  25% {
    bottom: 5px;
  }
  50% {
    bottom: 0px;
  }
  75% {
    bottom: -5px;
  }
  100% {
    bottom: 0px;
  }
}

.dynamic__text {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  height: 36px;
  color: #e1e1e3;
  overflow: hidden;
  margin-top: 50px;
  padding-left: 0;
}
.dynamic__text li {
  position: relative;
  top: 0;
  animation: slide 30s steps(3) infinite;
  padding-left: 0;
  cursor: default;
}
.dynamic__text li span {
  position: relative;
}

@keyframes slide {
  100% {
    top: -145px;
  }
}

.dynamic__text li span::after {
  content: "";
  left: 0;
  position: absolute;
  background-color: #1f3878;
  width: 100%;
  height: 100%;
  border-left: 2px solid #f96b24;
  animation: typing 10s steps(36) infinite;
}

@keyframes typing {
  50% {
    left: 100%;
    margin: 0 -35px 0 0;
  }
  100% {
    left: 0%;
    margin: 0 -35px 0 0;
  }
}
